(function() {
  'use strict';

  angular
    .module('documents', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('documents')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($document, $window) {
    var vm = this,
        __dialog;

    vm.blog_url = 'http://blog.ihsanberahim.com';

    vm.init = function()
    {
      __dialog = document.getElementById('coming-soon-dialog');

      (function(buttons)
      {
        angular.forEach(buttons, function(_elem, _key)
        {
          angular.element(_elem).on('click', function()
          {
            vm.doShowDialog();
          });
        });
      }(document.querySelectorAll('.show-coming-soon-dialog')));
    };
    vm.doShowDialog = function()
    {
      __dialog.showModal();
      __dialog.querySelector('.close').addEventListener('click', function() {
        __dialog.close();
      });
      __dialog.querySelector('.agree').addEventListener('click', function() {
        $window.open(vm.blog_url, '_self');
      });
    };
    vm.doCloseDialog = function()
    {
      __dialog.close();
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('documents')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('documents')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('documents')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('documents')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("documents").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div ng-init=main.init()><div class=\"mdl-card mdl-shadow--2dp\"><div class=mdl-card__title><h2 class=mdl-card__title-text>Welcome</h2></div><div class=mdl-card__supporting-text>I\'m here just to share everything the basic about AngularJs, React, Magento, Ionic. Let\'s get started at my blog.</div><div class=\"mdl-card__actions mdl-card--border\"><a class=\"mdl-button mdl-button--colored mdl-js-button mdl-js-ripple-effect\" ng-href={{main.blog_url}}>Go To Blog</a></div><div class=mdl-card__menu><button class=\"force-remove mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect\"><i class=material-icons>share</i></button></div></div></div><dialog id=coming-soon-dialog class=mdl-dialog><h4 class=mdl-dialog__title>Go To the Blog?</h4><div class=mdl-dialog__content><p>The section is under construction. Go to the blog first. Thanks.</p></div><div class=mdl-dialog__actions><button type=button class=\"mdl-button agree\">Agree</button> <button type=button class=\"mdl-button close\">Disagree</button></div></dialog>");}]);